@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  /* background: rgb(2, 0, 36); */
  background-size: 100% 100%;
  background: linear-gradient(
    90deg,
    rgb(190, 187, 255) 0%,
    rgb(201, 201, 255) 35%,
    rgb(253, 238, 255) 100%
  );
}
.CarCard {
  display: flex;
  justify-content: center;
  align-items: center;
}

.CarCardContainer {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  position: absolute;
  top: 200px;
  font-family: "Poppins", sans-serif;
  font-size: 0.8rem;
  font-weight: 700;
  border-radius: 10px;
}

.NoResults {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: absolute;
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
}
.CarMakeYear {
  position: absolute;
  top: 200px;
  z-index: 20;
  font-family: "Poppins", sans-serif;
  font-size: 0.8rem;
  text-align: center;
  padding: 0px 10px 0px 10px;
  border-radius: 10px;
}
.CarMakeYear h1 {
  font-weight: 900;
}

.CarCardInfo {
  width: 300px;
  background-color: rgb(255, 255, 255);
  margin: 20px;
  border-radius: 10px;
}

.CarCardText {
  display: flex;
  flex-direction: row;
  justify-content: left;
  height: 35px;
  width: 70%;
  align-items: center;
  padding-left: 20px;
  padding-right: 100px;
  padding-bottom: 20px;
}

.CarCardImage {
  width: 300px;
  height: 180px;
  overflow: hidden;
  border-radius: 10px 10px 0px 0px;
}

.CarCardImage img {
  max-width: 100%;
  /* max-height: 0%; */
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgb(32, 32, 32);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(85, 85, 85);
}

@media (max-width: 1919px) {
  .CarCardContainer {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 1500px) {
  .CarCardContainer {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 1500px) {
  .CarCardContainer {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 1080px) {
  .CarCardContainer {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 750px) {
  .CarCardContainer {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (max-width: 450px) {
  .CarCardContainer {
    top: 50px;
  }
}

.FetchData {
  width: 100vw;
  height: 100vh;
}
.EntryForm {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 20vh;
  font-family: "Poppins", sans-serif;
  font-size: 0.8rem;
  border-radius: 0px 0px 30px 30px;
  background-color: rgba(255, 255, 255, 0.938);
  display: flex;
  flex-direction: column;
}
.Form {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-right: 75px;
  margin-top: 20px;
  margin-left: 20px;
}

select {
  width: 13.2vw;
  padding: 12px 20px;
  margin-right: 13px;
  border: 2px solid rgb(190, 187, 255);
  border-radius: 4px;
  box-sizing: border-box;
}

label {
  font-weight: 800;
  display: flex;
  flex-direction: column;
}

input[type="text"] {
  width: 41vw;
  padding: 12px 20px;
  margin-top: 20px;
  border: 2px solid rgb(190, 187, 255);
  border-radius: 4px;
  box-sizing: border-box;
  margin-left: 20px;
}

input[type="submit"] {
  width: 10vw;
  background-color: rgb(156, 151, 255);
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type="submit"]:hover {
  background-color: rgb(125, 118, 255);
  transition: 0.5s;
}

@media (max-width: 769px) {
  .Form {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-right: 10px;
  }
  input[type="text"] {
    width: 92%;
  }
  select {
    width: 29.5vw;
  }
}

@media (max-width: 450px) {
  .EntryForm {
    height: 48%;
    width: 100%;
  }
  .Form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
    margin-left: 10px;
    margin-top: 50px;
  }
  input[type="text"] {
    width: 92%;
    margin-left: 15px;
    margin-top: 14px;
  }
  select {
    width: 99%;
    margin-bottom: 10px;
  }
  svg {
    padding-top: 15px;
    padding-left: 15px;
    transform: scale(2);
    z-index: 500;
    position: fixed;
  }
}


